<template>
	<div id="store-update">
		<public-header title="基本信息设置"></public-header>
		<div class="content">
			<van-cell-group v-if="type == 'title'">
				<van-field
					label="门店名称"
					placeholder="请输入门店名称"
					v-model="store.title"
					input-align="right"
				>
				</van-field>
			</van-cell-group>

			<van-cell-group v-else-if="type == 'telephone'">
				<van-field
					label="联系电话"
					placeholder="请输入门店联系电话"
					v-model="store.telephone"
					input-align="right"
				>
				</van-field>
			</van-cell-group>

			<van-cell-group v-else-if="type == 'address'">
				<van-field
					label="门店地址"
					placeholder="请输入门店地址"
					v-model="store.address"
					input-align="right"
				>
				</van-field>
			</van-cell-group>

			<template v-else-if="type == 'content'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">门店描述</div>
				<van-cell-group>
					<van-field v-model="store.content" type="textarea" rows="3" autosize placeholder="请输入门店描述" />
				</van-cell-group>
			</template>

			<template v-else-if="type == 'notice'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">商户公告</div>
				<van-cell-group>
					<van-field v-model="store.notice" type="textarea" rows="3" autosize placeholder="请输入商户公告" />
				</van-cell-group>
			</template>

			<template v-else-if="type == 'tips'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">商品列表页提示</div>
				<van-cell-group>
					<van-field v-model="store.tips" type="textarea" rows="3" autosize placeholder="用户进入页面后, 将弹框提示设置的内容" />
				</van-cell-group>
			</template>

			<template v-else-if="type == 'business_hours'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">营业时间</div>
				<van-cell-group>
					<van-cell v-for="(item, index) in store.business_hours" :key="index">
						<div slot="title">
							<span @click="onToggleTimePicker(index, 's')">{{item.s}}</span>
							<span class="padding-10-lr">至</span>
							<span @click="onToggleTimePicker(index, 'e')">{{item.e}}</span>
						</div>
						<div slot="right-icon" class="c-info" @click="onRemoveTime(index)">移除</div>
					</van-cell>
				</van-cell-group>
				<van-cell-group :class="{'margin-10-t': store.business_hours.length > 0}">
					<van-cell >
						<div class="c-info" slot="title" @click="onAddTime">
							<i class="icon icon-roundadd font-16"></i>添加时间段
						</div>
					</van-cell>
				</van-cell-group>
				<!--时间picker-->
				<van-popup v-model="timePickerShow" position="bottom">
					<van-datetime-picker
						type="time"
						@confirm="onConfirmTime"
						@cancel="onToggleTimePicker"
					/>
				</van-popup>
			</template>

			<!--门店Logo-->
			<template v-else-if="type == 'logo'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">门店Logo</div>
				<uploader max="1" :value="logo" @onGetUrl="onChangeLogo" @delete="onChangeLogo"></uploader>
			</template>

			<!--门店实景-->
			<template v-else-if="type == 'thumbs'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">门店实景</div>
				<uploader max="7" :value="thumbs" @onGetUrl="onChangeThumbs" @delete="onChangeThumbs"></uploader>
			</template>

			<!--好店分类设置-->
			<template v-else-if="type == 'haodian_category'">
				<van-cell title="好店分类" class="margin-10-t">
					<div slot="right-icon" class="flex" @click="onTogglePopup('haodian_category')">
						<span>{{store.haodian_cid > 0 ? store.haodian_category_cn : '未设置'}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-popup v-model="popup.haodian_category" position="bottom">
					<van-picker
							show-toolbar
							title="好店分类"
							:columns="columns"
							value-key="title"
							@cancel="onTogglePopup('haodian_category')"
							@confirm="onConfirmHaodianCategory"
							@change="onChangeHaodianCategory"
					/>
				</van-popup>
			</template>

			<!--好店标签设置-->
			<template v-else-if="type == 'haodian_tags'">
				<div class="tags-list">
					<div class="font-14 margin-10-r">好店标签</div>
					<div class="flex" v-if="store.haodian_tags && store.haodian_tags.length > 0">
						<div class="tag-group" v-for="(tag, tagIndex) in store.haodian_tags" :key="tagIndex">
							<van-tag class="bg-info" plain>{{tag}}</van-tag>
							<i class="icon icon-close" @click="onDeleteTag(tagIndex)"></i>
						</div>
					</div>
					<div v-else class="c-gray no-tags">
						暂未设置好店标签
					</div>
				</div>
				<van-cell-group>
					<van-field
						v-model="tag"
						label="添加标签"
						placeholder="请填写好店标签"
						icon="add"
						@click-icon="onAddTag"
					/>
				</van-cell-group>
			</template>

			<div class="padding-15">
				<van-button size="normal" class="bg-info" :disabled="!islegal" block @click="onSubmit">保存修改</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import uploader from "@/components/uploader"
	import {getData, setData} from "@/controller/funcCommon.js"
	export default {
		data() {
			return {
				timePickerShow: false,
				type: 'title',
				from: 'waimai',
				store: {
					title: '',
					logo: '',
					content: '', //描述
					business_hours: [],
					telephone: '',
					address: '',
					thumbs: [],
					category_arr: [],
					notice: '',
					tips: '',
					haodian_tags: []
				},
				categorys: [],
				businessHoursSelected: {
					index: 0,
					type: 's'
				},
				logo: [],
				thumbs: [],
				columns: [
					{
						values: [],
						defaultIndex: 0
					},
					{
						values: [],
						defaultIndex: 0
					},
				],
				popup: {
					haodian_category: false
				},
				tag: '',
				islegal: false,
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
			uploader
		},
		methods: {
			onToggleTimePicker(index, type) {
				this.timePickerShow = !this.timePickerShow;
				if(index > -1 && (type == 's' || type == 'e')) {
					this.businessHoursSelected.index = index;
					this.businessHoursSelected.type = type;
				}
			},
			onConfirmTime(value) {
				if(value) {
					let index = this.businessHoursSelected.index;
					let type = this.businessHoursSelected.type;
					this.store.business_hours[index][type] = value;
					this.onToggleTimePicker();
				}
			},
			onAddTime() {
				if(this.type != 'business_hours') {
					return false;
				}
				if(this.store.business_hours.length >=3 ) {
					this.util.$toast('最多可添加三个营业时间段')
					return false;
				}
				this.store.business_hours.push({
					s: '08:00',
					e: '21:00'
				});
			},
			onRemoveTime(index) {
				if(this.type != 'business_hours') {
					return false;
				}
				this.store.business_hours.splice(index, 1);
			},
			onToggleCategory(id, index) {
				this.$refs.checkboxes[index].toggle();
			},
			onChangeLogo(data) {
				let logo = ''
				if(data && data.length == 1 && data[0].filename) {
					logo = data[0].filename;
				}
				this.store.logo = logo;
			},
			onChangeThumbs(data) {
				let thumbs = [];
				if(data && data.length > 0) {
					for(var x in data) {
						thumbs.push({
							image: data[x].url,
							url: ''
						})
					}
				}
				this.store.thumbs = thumbs;
			},
			onTogglePopup(key) {
				this.popup[key] = !this.popup[key];
			},
			onConfirmHaodianCategory(picker) {
				if(picker[1]) {
					this.store.haodian_cid = picker[1].parentid;
					this.store.haodian_child_id = picker[1].id;
					this.store.haodian_category_cn = picker[0].title + '-' + picker[1].title;
				} else {
					this.store.haodian_cid = picker[0].id;
					this.store.haodian_child_id = 0;
					this.store.haodian_category_cn = picker[0].title;
				}
				this.onTogglePopup('haodian_category');
			},
			onChangeHaodianCategory(picker, value) {
				if(value[0].child && value[0].child.length > 0) {
					picker.setColumnValues(1, value[0].child);
				} else {
					picker.setColumnValues(1, []);
				}
			},
			onDeleteTag(index) {
				this.store.haodian_tags.splice(index, 1);
			},
			onAddTag() {
				var that = this;
				if(that.store.haodian_tags.length == 4) {
					that.util.$toast('好店标签最多只能添加四个');
					that.tag = '';
				} else {
					if(!that.tag) {
						that.util.$toast('请输入有效的好店标签');
					} else {
						that.store.haodian_tags.push(that.tag);
						that.tag = '';
					}
				}
			},
			onLoad() {
				var that = this;
				getData({
					url: 'manage/shop/index/info',
					vue: that,
					autoAssign: true,
					variable: 'store',
					data: {
						id: that.id,
						type: that.type
					},
					success: function(res) {
						that.islegal = true;
						if(that.type == 'logo') {
							if(that.store.logo) {
								that.logo.push({
									url: that.store.logo
								})
							}
						} else if(that.type == 'thumbs') {
							if(that.store.thumbs && that.store.thumbs.length > 0) {
								for(var x in that.store.thumbs) {
									that.thumbs.push({
										url: that.store.thumbs[x].image
									})
								}
							}
						} else if(that.type == 'haodian_category') {
							if(res.haodian_category) {
								var categorys = res.haodian_category;
								that.columns[0].values = categorys;
								if(that.columns[0].values[0].child) {
									that.columns[1].values = categorys[0]['child'];
								}
							}
						}
					}
				})
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				that.islegal = false;
				let value = '';
				if(that.type == 'haodian_category') {
					value = {
						haodian_cid: that.store.haodian_cid,
						haodian_child_id: that.store.haodian_child_id,
					}
				} else {
					value = that.store[that.type];
				}
				let redirect = 'pages/shop/basic?from=' + that.from;
				if(that.type == 'notice' || that.type == 'tips') {
					redirect = 'pages/shop/index';
				}
				setData({
					url: 'manage/shop/index/setting',
					vue: that,
					data: {
						type: that.type,
						value: JSON.stringify(value),
					},
					redirect: that.util.getUrl({path: redirect}),
					fail: function(res) {
						that.util.$toast(res);
						that.islegal = true;
						return;
					}
				})
			}
		},
		created() {
			if(this.$route.query) {
				if(this.$route.query.type) {
					this.type = this.$route.query.type;
				}
				if(this.$route.query.from) {
					let from = this.$route.query.from;
					let arr = ['waimai', 'haodian'];
					if(arr.indexOf(from) != -1) {
						this.from = from;
					}
				}
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-update .tags-list{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		padding: 10px 15px;
		font-size: 14px;
		background-color: #fff;
	}
	#store-update .tags-list .tag-group{
		position: relative;
	}
	#store-update .tags-list .van-tag{
		margin-right: 10px;
		max-width: 58px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#store-update .tags-list .icon-close{
		position: absolute;
		top: -4px;
		right: 2px;
		font-size: 12px;
		padding: 1px;
		border-radius: 100%;
		background-color: #ff2d4b;
		color: #fff;
		z-index: 99;
	}
	#store-update .tags-list .no-tags{
		text-align: right;
		line-height: 22px;
	}
	#store-update .van-cell__value .van-icon-add{
		color: #33aafc;
	}
</style>


